import React, { createContext, useState } from "react";

const DonationContext = createContext();

const DonationContextProvider = ({ children }) => {
  const [donationStep, setDonationStep] = useState(1);
  const [donationAmount, setDonationAmount] = useState(10);
  const [donatorFirstName, setDonatorFirstName] = useState();
  const [donatorLastName, setDonatorLastName] = useState();
  const [donatorEmail, setDonatorEmail] = useState();

  const [stripePromise, setStripePromise] = useState(null);
  const [clientSecret, setClientSecret] = useState("");

  return (
    <DonationContext.Provider
      value={{
        donationStep: donationStep,
        setDonationStep: setDonationStep,
        donationAmount: donationAmount,
        setDonationAmount: setDonationAmount,
        donatorFirstName: donatorFirstName,
        setDonatorFirstName: setDonatorFirstName,
        donatorLastName: donatorLastName,
        setDonatorLastName: setDonatorLastName,
        donatorEmail: donatorEmail,
        setDonatorEmail: setDonatorEmail,
      }}
    >
      {children}
    </DonationContext.Provider>
  );
};

export { DonationContext, DonationContextProvider };
