import React, { useState } from "react";
import NavMenuItem from "./NavMenuItem";

const NavMenu = () => {
  const [isMenuOpen, setMenuOpen] = useState(false);
  return (
    <div>
      <button
        onClick={() => setMenuOpen(!isMenuOpen)}
        className="aspect-square h-11 w-11 lg:hidden"
      >
        <div className="aspect-square border flex items-center justify-center h-full rounded w-full">
          <div className="flex flex-col gap-1">
            <div
              className={`bg-black duration-300 h-1 w-6 ${
                isMenuOpen ? "rotate-45 translate-y-2" : "rotate-0"
              }`}
            ></div>
            <div
              className={`bg-black duration-300 h-1 w-6 ${
                isMenuOpen ? "opacity-0" : "opacity-100"
              }`}
            ></div>
            <div
              className={`bg-black duration-300 h-1 w-6 ${
                isMenuOpen ? "-rotate-45 -translate-y-2" : "rotate-0"
              }`}
            ></div>
          </div>
        </div>
      </button>
      <div
        className={`max-lg:absolute bg-white duration-300 max-lg:h-theme-height-dvh-full max-lg:inset-x-0 max-lg:top-full max-lg:translate-y-px ${
          isMenuOpen ? "max-lg:translate-x-0" : "max-lg:translate-x-full"
        }`}
      >
        <ul className="flex flex-col gap-3 max-lg:p-4 md:flex-row">
          <NavMenuItem to={"/startseite"} icon={"house"} title={"Poetna"} />
          <NavMenuItem
            to={"/project"}
            icon={"rectangle-history"}
            title={"O projektu"}
          />
          <NavMenuItem to={"/plan"} icon={"diagram-subtask"} title={"Plan"} />
          <NavMenuItem to={"/galerie"} icon={"images"} title={"Galerija"} />
        </ul>
      </div>
    </div>
  );
};

export default NavMenu;
