import React from "react";
import {
  Section,
  SectionMaxContent,
  SectionFlexContent,
  SectionFlex,
  SectionStickyContent,
  SectionPadding,
} from "../components/section/Section";
import SectionHeading from "../components/typography/SectionHeading";
import SectionParagraph from "../components/typography/SectionParagraph";
import FlexSectionParagraph from "../components/flex/FlexSectionParagraph";
import FlexSectionHeading from "../components/flex/FlexSectionHeading";

import image01 from "../assets/media/img/01.jpg";
import { useTranslation } from "react-i18next";
import ButtonBackgroundNavigateIn from "../components/navigation/ButtonBackgroundNavigateIn";

const AboutSection = () => {
  const { t } = useTranslation();
  const pageTranslation = t("sections.about", { returnObjects: true });
  return (
    <Section>
      <SectionPadding>
        <SectionMaxContent>
          <SectionFlex>
            <SectionFlexContent>
              <SectionStickyContent>
                <img className="rounded-xl w-full" src={image01} />
              </SectionStickyContent>
            </SectionFlexContent>
            <SectionFlexContent>
              <FlexSectionHeading>
                <SectionHeading>{pageTranslation.heading}</SectionHeading>
                <FlexSectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[0]}
                  </SectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[1]}
                  </SectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[2]}
                  </SectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[3]}
                  </SectionParagraph>
                </FlexSectionParagraph>
                <ButtonBackgroundNavigateIn
                  to={"/spenden"}
                  icon={"hand-holding-heart"}
                  text={"Podri Izgradnju Donacijom"}
                />
              </FlexSectionHeading>
            </SectionFlexContent>
          </SectionFlex>
        </SectionMaxContent>
      </SectionPadding>
    </Section>
  );
};

export default AboutSection;
