import React from "react";

const SectionHeading = ({ children }) => {
  return (
    <h1 className="font-bold mt-6 pr-4 text-3xl md:pr-6 md:text-3xl xl:pr-8 xl:font-bold xl:text-4xl 2xl:pr-12 2xl:leading-tight 2xl:text-5xl">
      {children}
    </h1>
  );
};

export default SectionHeading;
