import React from "react";
import { Link } from "react-router-dom";
import Icon from "../icon/Icon";

const ButtonBackgroundNavigateIn = ({ tw, to, icon, text }) => {
  return (
    <Link
      to={to}
      className={`bg-theme-color-brand-primary block flex items-center justify-center gap-4 p-4 rounded text-theme-color-brand-primary-contrast md:w-fit md:px-6 ${
        tw ? tw : ""
      }`}
    >
      <div className="text-xl md:text-2xl xl:text-3xl">
        <Icon icon={icon} iconStyle="solid" />
      </div>
      <span className="font-bold leading-4 md:text-lg xl:text-xl">{text}</span>
    </Link>
  );
};

export default ButtonBackgroundNavigateIn;
