import React, { useContext, useState, useEffect } from "react";
import { DonationContext } from "../../../contexts/DonationContextProvider";
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import StripeForm from "../../stripe/StripeForm";

const Test = () => {
  const [stripePromise, setStripePromise] = useState();
  const [clientSecret, setClientSecret] = useState();
  const donationContext = useContext(DonationContext);
  const donationAmount = donationContext.donationAmount;

  useEffect(() => {
    setStripePromise(
      loadStripe(
        "pk_test_51OnhKnF168sPaYTBIVLrlrp35iDJ9YG90lfu03dC8pKpt1YZ4oyKA3Q9QM7hOWDdQcv0jfsxIdM7zFwCbLZXNwH000Eeazare1"
      )
    );
    fetch("https://api.grower.agency/CMDB/stripe/create-payment-intent", {
      method: "POST",
      body: JSON.stringify({ amount: donationAmount }),
    }).then(async (result) => {
      var { clientSecret } = await result.json();
      console.log(clientSecret);
      setClientSecret(clientSecret);
    });
  }, []);
  return (
    <>
      {clientSecret && stripePromise && (
        <Elements stripe={stripePromise} options={{ clientSecret }}>
          <StripeForm />
        </Elements>
      )}
    </>
  );
};

export default Test;
