import React, { useState } from "react";
import DonateAmountForm from "./donate/DonateAmountForm";
import { DonationContextProvider } from "../../contexts/DonationContextProvider";
import DonateDonatorForm from "./donate/DonateDonatorForm";
import DonateCheckoutForm from "./donate/DonateCheckoutForm";

const DonateForm = () => {
  return (
    <DonationContextProvider>
      <DonateAmountForm />
      <DonateDonatorForm />
      <DonateCheckoutForm />
    </DonationContextProvider>
  );
};

export default DonateForm;
