import React from "react";
import banner from "../../assets/media/img/banner.jpg";
import Icon from "../icon/Icon";

const HomeBanner = () => {
  return (
    <section className="h-theme-height-svh-full--header min-h-96 overflow-hidden relative">
      <div className="absolute inset-0">
        <img src={banner} className="h-full object-cover w-full" />
        <div className="absolute bg-black opacity-80 inset-0"></div>
      </div>
      <div className="flex h-full items-end px-4 py-8 relative text-white md:items-center md:text-center">
        <div className="mx-auto max-w-4xl flex flex-col gap-8 md:gap-16">
          <div className="flex flex-col gap-4 md:gap-8">
            <h1 className="font-krona-one font-bold leading-normal text-3xl md:text-5xl md:leading-snug xl:text-6xl xl:leading-relaxed">
              Bonjaki Kulturni Centar u Dortmundu
            </h1>
            <p className="font-medium text-lg md:text-xl xl:text-2xl">
              Allahove damije grade i odravaju oni koji u Allaha i u Onaj
              svijet vjeruju...
              <br /> Kur'an (Et-Tevbe, 18)
            </p>
          </div>
          <div className="bg-theme-color-brand-primary flex items-center gap-3 p-4 rounded md:mx-auto md:w-fit">
            <div className="text-xl md:text-2xl xl:text-3xl">
              <Icon icon={"hand-holding-heart"} iconStyle="solid" />
            </div>
            <span className="font-bold leading-4 md:text-lg xl:text-xl">
              Podri Izgradnju BKC-a u Dortmundu
            </span>
          </div>
        </div>
      </div>
    </section>
  );
};

export default HomeBanner;
