import React, { useContext, useEffect, useState } from "react";
import { DonationContext } from "../../../contexts/DonationContextProvider";
import Icon from "../../icon/Icon";
import Input from "../Input";
import FormError from "../FormError";

const DonateDonatorForm = () => {
  const donationContext = useContext(DonationContext);
  const [formError, setFormError] = useState();

  const donatorFirstName = donationContext.donatorFirstName;
  const setDonatorFirstName = donationContext.setDonatorFirstName;
  const donatorLastName = donationContext.donatorLastName;
  const setDonatorLastName = donationContext.setDonatorLastName;
  const donatorEmail = donationContext.donatorEmail;
  const setDonatorEmail = donationContext.setDonatorEmail;
  const donationStep = donationContext.donationStep;
  const setDonationStep = donationContext.setDonationStep;

  const handleForm = () => {
    setFormError();
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!donatorFirstName) {
      setFormError("Molimo vas unesite vae Ime");
    } else if (!donatorLastName) {
      setFormError("Molimo vas unesite vae Prezime");
    } else if (!donatorEmail) {
      setFormError("Molimo vas unesite vau Email adresu");
    } else if (!emailRegex.test(donatorEmail)) {
      setFormError("Molimo vas unesite validnu Email adresu");
    } else {
      setDonationStep(3);
    }
  };
  return (
    <>
      {donationStep === 2 ? (
        <div className="bg-white border flex flex-col gap-y-8 max-w-md mx-auto p-6 rounded text-center">
          <div className="flex flex-col gap-y-4">
            <h2 className="font-medium text-2xl">Vae Informacije</h2>
            <div className="flex flex-col gap-2">
              <div className="flex items-center gap-2">
                <Input
                  icon={"user"}
                  placeholder={"Ime"}
                  value={donatorFirstName}
                  onChange={(e) => setDonatorFirstName(e.target.value)}
                />
                <Input
                  icon={"user"}
                  placeholder={"Prezime"}
                  value={donatorLastName}
                  onChange={(e) => setDonatorLastName(e.target.value)}
                />
              </div>
              <Input
                icon={"envelope"}
                placeholder={"Email adresa"}
                value={donatorEmail}
                onChange={(e) => setDonatorEmail(e.target.value)}
              />
              <div className="flex items-center gap-2 mt-2 text-left">
                <input className="h-4 w-4" type="checkbox" />
                <span>Doniraj anonimno</span>
              </div>
            </div>
          </div>
          {formError ? <FormError>{formError}</FormError> : null}
          <div className="flex items-center gap-4">
            <button
              onClick={() => setDonationStep(1)}
              className="bg-white border flex-1 flex items-center justify-center gap-4 h-12 p-4 rounded md:w-fit md:px-6"
            >
              <div className="text-xl md:text-xl xl:text-2xl">
                <Icon icon={"arrow-left-long"} iconStyle="solid" />
              </div>
              <span className="font-bold leading-4 md:text-lg xl:text-xl">
                Nazad
              </span>
            </button>
            <button
              onClick={() => handleForm()}
              className="bg-theme-color-brand-primary flex-1 flex items-center justify-center gap-4 h-12 p-4 rounded text-theme-color-brand-primary-contrast md:w-fit md:px-6"
            >
              <span className="font-bold leading-4 md:text-lg xl:text-xl">
                Dalje
              </span>
              <div className="text-xl md:text-xl xl:text-2xl">
                <Icon icon={"arrow-right-long"} iconStyle="solid" />
              </div>
            </button>
          </div>
        </div>
      ) : null}
    </>
  );
};

export default DonateDonatorForm;
