import React from "react";
import Icon from "../icon/Icon";

const Input = ({ icon, type = "text", placeholder, value, onChange }) => {
  return (
    <div className="border flex items-center h-12 overflow-hidden rounded">
      <div className="h-12 flex items-center justify-center text-theme-color-brand-primary text-xl w-12">
        <Icon icon={icon} iconStyle="regular" />
      </div>
      <input
        className="font-semibold h-full outline-none w-full"
        type={type}
        placeholder={placeholder}
        value={value}
        onChange={onChange}
      />
    </div>
  );
};

export default Input;
