import React from "react";
import {
  Section,
  SectionMaxContent,
  SectionFlexContent,
  SectionFlex,
  SectionStickyContent,
  SectionPadding,
} from "../components/section/Section";
import SectionHeading from "../components/typography/SectionHeading";
import SectionParagraph from "../components/typography/SectionParagraph";
import FlexSectionParagraph from "../components/flex/FlexSectionParagraph";
import FlexSectionHeading from "../components/flex/FlexSectionHeading";
import { useTranslation } from "react-i18next";

import ButtonBackgroundNavigateIn from "../components/navigation/ButtonBackgroundNavigateIn";

const LocationSection = () => {
  const { t } = useTranslation();
  const pageTranslation = t("sections.location", { returnObjects: true });
  return (
    <Section>
      <SectionPadding>
        <SectionMaxContent>
          <SectionFlex flexDirection="md:flex-row-reverse">
            <SectionFlexContent>
              <SectionStickyContent>
                <iframe
                  className="aspect-video border-0 w-full"
                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2482.027588975857!2d7.472693877071673!3d51.531053808974434!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x47b910ab22276b25%3A0x5171767b600f537!2sJuliusstra%C3%9Fe%2034%2C%2044145%20Dortmund%2C%20Njema%C4%8Dka!5e0!3m2!1sbs!2srs!4v1708206050324!5m2!1sbs!2srs"
                  allowFullScreen
                  loading="async lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                ></iframe>
              </SectionStickyContent>
            </SectionFlexContent>
            <SectionFlexContent>
              <FlexSectionHeading>
                <SectionHeading>{pageTranslation.heading}</SectionHeading>
                <FlexSectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[0]}
                  </SectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[1]}
                  </SectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[2]}
                  </SectionParagraph>
                </FlexSectionParagraph>
                <ButtonBackgroundNavigateIn
                  to={"/spenden"}
                  icon={"hand-holding-heart"}
                  text={"Podri Izgradnju Donacijom"}
                />
              </FlexSectionHeading>
            </SectionFlexContent>
          </SectionFlex>
        </SectionMaxContent>
      </SectionPadding>
    </Section>
  );
};

export default LocationSection;
