import React from "react";
import {
  Section,
  SectionBackgroundColor,
  SectionMaxContent,
  SectionPadding,
} from "../components/section/Section";
import SectionHeading from "../components/typography/SectionHeading";
import FlexSectionHeading from "../components/flex/FlexSectionHeading";
import { useTranslation } from "react-i18next";

import DonateForm from "../components/form/DonateForm";

const DonateSection = () => {
  const { t } = useTranslation();
  const pageTranslation = t("sections.video", { returnObjects: true });
  return (
    <Section>
      <SectionBackgroundColor bg="bg-slate-100">
        <SectionPadding>
          <SectionMaxContent>
            <FlexSectionHeading>
              <div className="text-center">
                <SectionHeading>Donirajte</SectionHeading>
              </div>
              <DonateForm />
            </FlexSectionHeading>
          </SectionMaxContent>
        </SectionPadding>
      </SectionBackgroundColor>
    </Section>
  );
};

export default DonateSection;
