import React from "react";
import Icon from "../icon/Icon";

const ShareItem = ({ href, icon, platform }) => {
  return (
    <li className="aspect-square bg-white border rounded shadow-sm w-fit">
      <a
        href={href}
        className="block flex flex-col items-center justify-center gap-2 h-full px-4 py-8 rounded text-center xl:gap-4 xl:px-6 xl:py-12 2xl:gap-6 2xl:px-8 2xl:py-16"
      >
        <div className="text-theme-color-brand-primary text-3xl xl:text-4xl 2xl:text-5xl">
          <Icon icon={icon} iconStyle="brands" />
        </div>
        <span className="font-semibold leading-5 xl:text-lg 2xl:text-xl">
          {platform}
        </span>
      </a>
    </li>
  );
};

export default ShareItem;
