import React from "react";
import {
  Section,
  SectionMaxContent,
  SectionFlexContent,
  SectionFlex,
  SectionStickyContent,
  SectionPadding,
} from "../components/section/Section";
import SectionHeading from "../components/typography/SectionHeading";
import SectionParagraph from "../components/typography/SectionParagraph";
import FlexSectionParagraph from "../components/flex/FlexSectionParagraph";
import FlexSectionHeading from "../components/flex/FlexSectionHeading";

import image06 from "../assets/media/img/06.jpg";
import { useTranslation } from "react-i18next";
import Icon from "../components/icon/Icon";
import SectionSubHeading from "../components/typography/SectionSubHeading";
import ButtonBackgroundNavigateIn from "../components/navigation/ButtonBackgroundNavigateIn";

const PlanSection = () => {
  const { t } = useTranslation();
  const pageTranslation = t("sections.plan", { returnObjects: true });
  return (
    <Section>
      <SectionPadding>
        <SectionMaxContent>
          <SectionFlex>
            <SectionFlexContent>
              <SectionStickyContent>
                <img className="rounded-xl w-full" src={image06} />
              </SectionStickyContent>
            </SectionFlexContent>
            <SectionFlexContent>
              <FlexSectionHeading>
                <SectionHeading>{pageTranslation.heading}</SectionHeading>
                <FlexSectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[0]}
                  </SectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[1]}
                  </SectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[2]}
                  </SectionParagraph>
                </FlexSectionParagraph>
              </FlexSectionHeading>
            </SectionFlexContent>
          </SectionFlex>
          <div className="flex flex-col gap-6 mt-12 text-center">
            <SectionSubHeading>Prostorije</SectionSubHeading>
            <ul className="grid grid-cols-3 gap-2 md:grid-cols-4 md:gap-4 lg:grid-cols-6 lg:gap-6">
              {pageTranslation.rooms.map((room, key) => {
                return (
                  <li
                    key={key}
                    className="aspect-square border flex flex-col gap-2 items-center justify-center rounded-lg w-full"
                  >
                    <div className="flex flex-col gap-2 items-center justify-center p-4 text-center">
                      <div className="text-3xl text-theme-color-brand-primary md:text-4xl">
                        <Icon icon={room.icon} />
                      </div>
                      <h3 className="font-semibold leading-4 text-sm md:text-base lg:text-xl">
                        {room.heading}
                      </h3>
                    </div>
                  </li>
                );
              })}
            </ul>
            <ButtonBackgroundNavigateIn
              tw={"md:mx-auto"}
              to={"/spenden"}
              icon={"hand-holding-heart"}
              text={"Podri Izgradnju Donacijom"}
            />
          </div>
        </SectionMaxContent>
      </SectionPadding>
    </Section>
  );
};

export default PlanSection;
