import i18n from "i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import { initReactI18next } from "react-i18next";
import bs from "./app/locales/bs.json";

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    supportedLngs: ["bs"],
    fallbackLng: "bs",
    detection: {
      order: ["htmlTag", "navigator", "cookie"],
    },
    caches: ["cookie"],
    resources: {
      bs: { translation: bs },
    },
    interpolation: {
      escapeValue: false,
    },
  });

