import React from "react";
import {
  Section,
  SectionBackgroundColor,
  SectionMaxContent,
  SectionPadding,
} from "../components/section/Section";
import SectionHeading from "../components/typography/SectionHeading";
import FlexSectionHeading from "../components/flex/FlexSectionHeading";
import { useTranslation } from "react-i18next";

const VideoSection = () => {
  const { t } = useTranslation();
  const pageTranslation = t("sections.video", { returnObjects: true });
  return (
    <Section>
      <SectionBackgroundColor bg="bg-slate-100">
        <SectionPadding>
          <SectionMaxContent>
            <FlexSectionHeading>
              <div className="text-center">
                <SectionHeading>{pageTranslation.heading}</SectionHeading>
              </div>
              <iframe
                className="aspect-video border-0 mx-auto max-w-screen-lg w-full"
                src="https://www.youtube.com/embed/uXxXm8bMcY4?si=Z7PV7CdMiBq5ppQw"
                title="YouTube video player"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowFullScreen
              ></iframe>
            </FlexSectionHeading>
          </SectionMaxContent>
        </SectionPadding>
      </SectionBackgroundColor>
    </Section>
  );
};

export default VideoSection;
