import React from "react";
import {
  Section,
  SectionMaxContent,
  SectionFlexContent,
  SectionFlex,
  SectionStickyContent,
  SectionPadding,
} from "../components/section/Section";
import SectionHeading from "../components/typography/SectionHeading";
import SectionParagraph from "../components/typography/SectionParagraph";
import FlexSectionParagraph from "../components/flex/FlexSectionParagraph";
import FlexSectionHeading from "../components/flex/FlexSectionHeading";

import image013 from "../assets/media/img/013.jpg";
import { useTranslation } from "react-i18next";

import ButtonNavigateIn from "../components/navigation/ButtonNavigateIn";
import ShareItem from "../components/share/ShareItem";
import ListItem from "../components/list/ListItem";
import SectionSubHeading from "../components/typography/SectionSubHeading";

const SupportSection = () => {
  const { t } = useTranslation();
  const pageTranslation = t("sections.support", { returnObjects: true });
  return (
    <Section>
      <SectionPadding>
        <SectionMaxContent>
          <SectionFlex flexDirection="md:flex-row-reverse">
            <SectionFlexContent>
              <SectionStickyContent>
                <img className="rounded-xl w-full" src={image013} />
              </SectionStickyContent>
            </SectionFlexContent>
            <SectionFlexContent>
              <FlexSectionHeading>
                <SectionHeading>{pageTranslation.heading}</SectionHeading>
                <FlexSectionParagraph>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[0]}
                  </SectionParagraph>
                </FlexSectionParagraph>
              </FlexSectionHeading>
              <div className="flex flex-col md:mt-4 md:gap-y-4 lg:gap-y-6 lg:mt-6 xl:gap-y-8 xl:mt-8">
                <div>
                  <SectionSubHeading>
                    {pageTranslation.subheadings[0]}
                  </SectionSubHeading>
                  <ul className="flex items-stretch gap-3 my-4 xl:gap-5 2xl:gap-7">
                    <ListItem>
                      <ButtonNavigateIn
                        to={"/spenden/online"}
                        icon="globe"
                        text={pageTranslation.support.online}
                      />
                    </ListItem>
                    <ListItem>
                      <ButtonNavigateIn
                        to={"/spenden/direct"}
                        icon="bank"
                        text={pageTranslation.support.direct}
                      />
                    </ListItem>
                  </ul>
                  <FlexSectionParagraph>
                    <SectionParagraph>
                      {pageTranslation.paragraphs[1]}
                    </SectionParagraph>
                  </FlexSectionParagraph>
                </div>
                <div>
                  <SectionSubHeading>
                    {pageTranslation.subheadings[1]}
                  </SectionSubHeading>
                  <ul className="flex items-stretch gap-3 my-4 xl:gap-5 2xl:gap-7">
                    <ShareItem
                      href={`https://wa.me/?text=${pageTranslation.share}`}
                      icon="whatsapp"
                      platform="Whatsapp"
                    />
                    <ShareItem
                      href={`https://twitter.com/intent/tweet?text=${pageTranslation.share}`}
                      icon={"x-twitter"}
                      platform="X (Twitter)"
                    />
                  </ul>
                  <SectionParagraph>
                    {pageTranslation.paragraphs[2]}
                  </SectionParagraph>
                </div>
              </div>
            </SectionFlexContent>
          </SectionFlex>
        </SectionMaxContent>
      </SectionPadding>
    </Section>
  );
};

export default SupportSection;
