import React from "react";

const Section = ({ children }) => {
  return <section className="border-b">{children}</section>;
};

const SectionPadding = ({ children }) => {
  return <div className="px-4 py-8 relative md:p-12 xl:p-24">{children}</div>;
};

const SectionBackgroundColor = ({
  bg = "bg-theme-color-section-background",
  children,
}) => {
  return <div className={`${bg}`}>{children}</div>;
};

const SectionMaxContent = ({
  maxWidth = "max-w-theme-width-max-content",
  children,
}) => {
  return <div className={`mx-auto ${maxWidth}`}>{children}</div>;
};

const SectionStickyContent = ({ top = "top-24", children }) => {
  return <div className={`sticky ${top}`}>{children}</div>;
};

const SectionFlex = ({ flexDirection = "md:flex-row", children }) => {
  return (
    <div
      className={`flex flex-col items-stretch ${flexDirection} md:gap-x-8 xl:gap-x-16`}
    >
      {children}
    </div>
  );
};

const SectionFlexContent = ({ tw = "", flex = "flex-1", children }) => {
  return <div className={`${flex}${tw}`}>{children}</div>;
};

const SectionFlexContentSticky = ({ flex = "flex-1", children }) => {
  return (
    <SectionFlexContent tw={" sticky top-24"} flex={flex}>
      {children}
    </SectionFlexContent>
  );
};

export {
  Section,
  SectionPadding,
  SectionBackgroundColor,
  SectionMaxContent,
  SectionStickyContent,
  SectionFlex,
  SectionFlexContent,
  SectionFlexContentSticky,
};
