import React from "react";
import { Link } from "react-router-dom";
import Icon from "../icon/Icon";

const ButtonNavigateIn = ({ to, icon, text }) => {
  return (
    <Link
      to={to}
      className="block flex flex-col items-center justify-center gap-2 h-full px-4 py-8 rounded text-center xl:gap-4 xl:px-6 xl:py-12 2xl:gap-6 2xl:px-8 2xl:py-16"
    >
      <div className="text-theme-color-brand-primary text-3xl xl:text-4xl 2xl:text-5xl">
        <Icon icon={icon} />
      </div>
      <span className="font-semibold leading-5 max-w-60 xl:text-lg 2xl:text-xl">
        {text}
      </span>
    </Link>
  );
};

export default ButtonNavigateIn;
