import React, { useContext, useState } from "react";
import { DonationContext } from "../../../contexts/DonationContextProvider";
import Icon from "../../icon/Icon";
import Input from "../Input";
import FormError from "../FormError";

const DonateAmountForm = () => {
  const [formError, setFormError] = useState();
  const donationContext = useContext(DonationContext);
  const donationAmount = donationContext.donationAmount;
  const setDonationAmount = donationContext.setDonationAmount;
  const donationStep = donationContext.donationStep;
  const setDonationStep = donationContext.setDonationStep;

  const handleForm = () => {
    setFormError();
    if (donationAmount >= 10) {
      setDonationStep(2);
    } else {
      setFormError("Iznos mora biti jednak ili vei od 10.");
    }
  };
  return (
    <>
      {donationStep === 1 ? (
        <div className="bg-white border flex flex-col gap-y-4 max-w-md mx-auto p-6 rounded text-center w-full">
          <div className="flex flex-col gap-y-4">
            <h2 className="font-medium text-2xl">Odaberite Iznos</h2>
            <div className="flex items-center justify-between">
              <button
                onClick={() => setDonationAmount(10)}
                className={`border font-semibold px-4 py-2 rounded ${
                  donationAmount === 10
                    ? "bg-theme-color-brand-primary text-theme-color-brand-primary-contrast"
                    : ""
                }`}
              >
                10
              </button>
              <button
                onClick={() => setDonationAmount(20)}
                className={`border font-semibold px-4 py-2 rounded ${
                  donationAmount === 20
                    ? "bg-theme-color-brand-primary text-theme-color-brand-primary-contrast"
                    : ""
                }`}
              >
                20
              </button>
              <button
                onClick={() => setDonationAmount(50)}
                className={`border font-semibold px-4 py-2 rounded ${
                  donationAmount === 50
                    ? "bg-theme-color-brand-primary text-theme-color-brand-primary-contrast"
                    : ""
                }`}
              >
                50
              </button>
              <button
                onClick={() => setDonationAmount(100)}
                className={`border font-semibold px-4 py-2 rounded ${
                  donationAmount === 100
                    ? "bg-theme-color-brand-primary text-theme-color-brand-primary-contrast"
                    : ""
                }`}
              >
                100
              </button>
            </div>
          </div>
          <div className="relative text-lg before:absolute before:bg-slate-500 after:content-[''] before:h-px before:left-0 before:top-1/2 before:w-full">
            <span className="bg-white px-2 relative">ili</span>
          </div>
          <div className="flex flex-col gap-y-4">
            <h2 className="font-medium text-2xl">Unesite Iznos</h2>
            <Input
              icon={"circle-dollar-to-slot"}
              type="number"
              value={donationAmount}
              onChange={(e) => setDonationAmount(e.target.value)}
            />
          </div>
          {formError ? <FormError>{formError}</FormError> : null}
          <button
            onClick={() => handleForm()}
            className="bg-theme-color-brand-primary block flex items-center justify-center gap-4 h-12 p-4 rounded text-theme-color-brand-primary-contrast md:px-6"
          >
            <span className="font-bold leading-4 md:text-lg xl:text-xl">
              Dalje
            </span>
            <div className="text-xl md:text-xl xl:text-2xl">
              <Icon icon={"arrow-right-long"} iconStyle="solid" />
            </div>
          </button>
        </div>
      ) : null}
    </>
  );
};

export default DonateAmountForm;
